<template>
  <div id="paySuccess">
		<div class="pay_success_head" :style="backgroundImage1">
			<div class="success_icon_block">
				<img src="../../assets/order_icon_order.png" class="success_icon" />
				<p>预约成功</p>
			</div>
			<div class="success_head_msg">挂号信息已发送到手机 {{phone}}，若取消预约，请在就诊前一日24:00前进行取消。</div>
			<div class="return_btn_block">
				<button class="return" @click="() => {this.$router.push('/user/appointmentManagement')}">管理我的预约</button>
			</div>
		</div>

		<div class="pay_msg" :style="backgroundImage2">
			<div class="pay_msg_block">
				<div class="pay_msg_title">预约信息</div>
				<div v-for="item in orderMsgList">
					<div class="pay_msg_items">{{item.key}}：{{item.val}}</div>
				</div>
			</div>
			<div class="pay_msg_block">
				<div class="pay_msg_title">就诊人信息</div>
				<div v-for="item in personMsg">
					<div class="pay_msg_items">{{item.key}}：{{item.val}}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
  data () {
    return {
      backgroundImage1: {background: "url(" + require("../../assets/order_bg.png") + ") no-repeat center"},
      backgroundImage2: {backgroundImage: "url(" + require("../../assets/Order_info_bg.png") + ")"},
      phone: JSON.parse(sessionStorage.getItem('userInfo')).phone,
      orderMsgList: [
        {
          key: '预约信息',
          val: this.$store.state.wmodule.curHospitalName
        }, {
          key: '预约科室',
          val: this.$store.state.wmodule.curDeptName
        }, {
          key: '预约医生',
          val: this.$store.state.wmodule.curDoctorName
        }, {
          key: '就诊时间',
          val: this.$store.state.wmodule.curAppointmentDate + ' ' + this.$store.state.wmodule.curAppointmentStartTime + '-' + this.$store.state.wmodule.curAppointmentEndTime + " " + this.filterWeek(this.$store.state.wmodule.curAppointmentWeek)
        }, {
          key: '订单费用',
          val: this.$store.state.wmodule.curFee + '元'
        }, {
          key: '订单状态',
          val: '已支付'
        }, {
          key: '订单编号',
          val: this.$store.state.wmodule.curBillCode
        }
			],
      personMsg: [
        {
          key: '患者姓名',
          val: JSON.parse(sessionStorage.getItem('userInfo')).userName
        }, {
          key: '身份证号',
          val: JSON.parse(sessionStorage.getItem('userInfo')).idCard
        }
      ]
    }
  },
	methods: {
		filterWeek(val) {
			switch(val) {
				case "1":
					return '星期一';
				case "2":
					return '星期二';
				case "3":
					return '星期三';
				case "4":
					return '星期四';
				case "5":
					return '星期五';
				case "6":
					return '星期六';
				case "7":
					return '星期日';
				default: 
					return val;
			}
		}
	}
}
</script>

<style scoped lang="less">
	#paySuccess{
		padding-bottom: 1.3*2rem;
		.pay_success_head {
			margin-top: -.88rem;
			height: 5.32rem;
	    background: url('../../assets/user/order_bg.png') no-repeat;
			background-size: 100% 100%;
			color: #fff;
			padding: 0 .24*2rem;
			box-sizing: border-box;
			.success_icon_block {
				padding-top: .88rem;
				text-align: center;
				font-size: .16*2rem;
				line-height: .22*2rem;
				.success_icon {
					width: .8*2rem;
					margin-left: .1*2rem;
				}
			}
			.success_head_msg {
				font-size: .12*2rem;
				line-height: .18*2rem;
				margin-top: .06*2rem;
				opacity: .9;
			}
			.return_btn_block {
				padding: .12*2rem 0 .4*2rem 0;
				.return {
					width: 1.54*2rem;
					height: .44*2rem;
					padding: .12*2rem;
					font-size: .16*2rem;
					border: 1px solid #FFFFFF;
					border-radius: 4px;
					background-color: transparent;
					margin: 0 auto;
					display: block;
				}
			}
		}

		.pay_msg {
			padding: .14*2rem;
			background-image: url(../../assets/Order_info_bg.png);
			background-size: 100%;
			background-repeat: no-repeat;
			margin: -.2*2rem .15*2rem;
			box-shadow: 3px 3px 10px 5px rgba(51,145,232,0.10);
			border-radius: 4px;
			.pay_msg_block {
				color: #272727;
				font-size: .12*2rem;
				line-height: .18*2rem;
				margin-bottom: .1*2rem;
				&:last-child {
					margin-bottom: 0;
				}
				.pay_msg_title {
					font-weight: bold;
					font-size: .16*2rem;
					padding-bottom: .14*2rem;
				}
				.pay_msg_items {
					padding-bottom: .1*2rem;
					opacity: .8;
				}
			}
		}
	}
</style>



